	module.exports = {"data":{"markdownRemark":{"html":"<p>Today’s National Day, Beijing’s National Day party, is very warm and grand, and seems to be more enthusiastic than ever. The lights on the square are bright, colorful flowers, crowds, joys, laughter, a jubilant salute, fireworks, dancing, singing, ten winds. This year's fireworks are particularly high, especially good. Some varieties have not been seen in the past, and the amount is also large. In the sky, a large piece of cloth is produced, which is very beautiful, reflecting us, reflecting the earth and reflecting the sky.</p>\n<p>A nation must have its own solemn ceremony. This is the best way for a nation to feel its national integrity and national greatness. The individual molecules in the nation are often scattered in daily life, or in small groups, the overall feeling is not strong.</p>\n<p>In each day of the nation, there are often only cultural, economic, and artistic connections, and the grand rituals of the whole nation can tell people more important things. This is the physical and political connection of the spirit. Therefore, many countries in the world pay great attention to the rituals of their own nation, and National Day is the best opportunity.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0152.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0152.md","frontmatter":{"title":"October 1st Saturday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0152.md absPath of file >>> MarkdownRemark"}}

